.my-profile{
  display: flex;align-items: center;margin-bottom: 20px;padding-top: 10px;
  .img{
    flex: 0 0 80px;display: flex;align-items: center;justify-content: center;
    width: 80px;height: 80px;border-radius: 50rem;position: relative;overflow: hidden;
    &::before{content: '';border: 1px solid $border-color;border-radius: 50%;position: absolute;left: 0;top: 0;right: 0;bottom: 0;}
    img{width: 100%;height: 100%;object-fit: cover;position: relative;}
    img.default{object-fit: contain;width: auto;height: auto;}
  }
  .info{
    flex-grow: 1;margin-left: 15px;
    .username{
      display: block;font-size: 30px;font-weight: 700;line-height: 1.16666;margin-bottom: 3px;transform: translateY(-6px);
      .icon-full-menu-arr{width: 12px;height: 17px;}
    }
    .btn-friend-invite{padding: 9px 15px;font-size: 12px;border: 1px solid rgba(0,0,0,0.25);border-radius: 10rem;}
  }
}

.my-point{
  background: #f4f4f4;border-radius: 10px;padding: 20px 0;
  .point{
    display: flex;align-items: center;justify-content: center;
    .icon-point{width: 25px;height: 25px;margin-right: 7.5px;}
    strong{font-size: 30px;font-family: $font-family-eng;font-weight: 500;line-height: 1.1666;}
  }
  .menu{
    display: flex;justify-content: center;align-items: center;margin-top: 20px;
    a{font-size: 12px;font-weight: 700;flex: 1;text-align: center;color: rgba(0,0,0,0.8);}
  }
}

.my-lupick-point{
  text-align: center;padding: 30px 0 40px;border-bottom: 1px solid $border-color;position: sticky;top: 60px;background: #fff;
  .point{font-size: 30px;font-weight: 700;font-family: $font-family-eng;}
  .point-desc{
    font-size: 13px;color: $gray;font-weight: 700;margin-top: 8px;
    a{font-size: 13px;color: $gray;font-weight: 700;font-family: $font-family-eng, $font-family-base;}
  }
}

.my-point-history{
  .item{border-bottom: 1px solid $border-color;padding: 20px 0;}
  .title{font-size: 15px;}
  .date{font-size: 15px;color: $gray;margin-top: 5px;}
  .history{font-family: $font-family-eng;font-size: 18px;font-weight: 700;}
}

.my-event-state{
  margin-bottom: 30px;
  .section-title{font-size: 16px;}
  .my-event-step{margin-bottom: 20px;}
  .event-count{
    border-radius: 50%;background: #f4f4f4;width: 100%;height: 0;padding-bottom: 100%;position: relative;
    span{position: absolute;left: 50%;top: 50%;transform: translate(-50%, -50%);font-size: 15px;font-weight: 700;color: #8c8c8c;}
  }
  .event-order{
    font-size: 12px;line-height: 1.3333;text-align: center;margin-top: 9px;
  }
  .icon-step-arr-right{width: 8px;height: 100%;background-image: url('../images/icon-step-arrow-right.svg');}
  .col-auto{padding-bottom: 41px;}
}

.be-lucky{
  display: flex;flex-direction: column;text-align: center;align-items: center;padding-top: 90px;border-top: 10px solid rgba(0,0,0,0.05);
  p{font-size: 30px;font-weight: 700;display: inline-block;text-align: left;margin-bottom: 20px;}
  .btn{width: 160px;}
}

.my-event-list{
  .item{
    border-top: 8px solid rgba(0,0,0,0.05);padding: 30px 20px;display: block;
    &:first-of-type{border-top: 0;}
  }
  .item-top{padding-bottom: 15px;margin-bottom: 15px;border-bottom: 1px solid $border-color;}
  .item-body{
    > a{display: block;}
  }
  .number{font-size: 12px;color: $gray;}
  .date{font-size: 12px;color: $gray;}
  .icon-arr-down-gray{transform: rotate(-90deg);margin: 0;}
  .info{
    margin-right: 30px;flex: 1 0 0%;min-width: 0;
    .state{color: $secondary;font-size: 16px;font-weight: 700;}
    .title{font-size: 12px;font-weight: 700;@include ellipsis();margin-top: 15px;}
    .product{font-size: 14px;@include ellipsis();margin-top: 3px;}
  }
  .img{
    flex: 0 0 80px;width:80px;height: 80px;position: relative;
    img{width: 100%;height: 100%;object-fit: cover;position: absolute;}
  }
  .price{font-size: 14px;font-weight: 700;line-height: 1.142;margin-top: 20px;}

  &.img-first{
    .item{border-top: 0;padding-bottom: 0;}
    .item:first-of-type{padding-top: 20px;}
    .info{margin-right: 0;margin-left: 10px;}
    .title{
      margin-top: 0;
      &.max-line-2{@include ellipsis-multiline(12,1.6,2,$height-fix: true);margin-bottom: 3px;}
    }
    .product{font-size: 12px;margin-bottom: 3px;}
    .product + .btn-sm{margin-top: 5px;}
  }

  .top-sort + &:not(.img-first){
    border-top: 8px solid rgba(0,0,0,0.05);margin-top: 20px;
  }
}

.congratulations{font-size: 16px;line-height: 1.375;color: $secondary;font-weight: 700;margin: 30px 0 10px;}

.setting-menu{
  padding-top: 10px;
  ul{
    border-bottom: 4px solid rgba(0,0,0,0.05);
    li{
      padding: 0 20px;
      > a{
        display: flex;align-items: center;justify-content: space-between;font-size: 16px;padding: 19px 0;border-bottom: 1px solid rgba(0,0,0,0.05);
        small{font-size: 10px;color: $gray-light;font-weight: 700;}
      }
    }
  }
}

.collaboration{
  padding: 20px 20px 0;
  a{display: flex;align-items: center;justify-content: space-between;border: 1px solid $border-color;padding: 23px 15px;border-radius: 7px;}
  .logo{
    background: url('../images/logo.svg') no-repeat 20px 50%;background-size: contain;width: 100px;height: 25px;position: relative;padding-left: 20px;display: inline-block;vertical-align: middle;
    &::before{content: '';width: 40px;height: 40px;background: $primary;border-radius: 50%;position: absolute;left: 0;top: 50%;transform: translateY(-50%);z-index: -1;}
  }
  .txt{font-size: 15px;font-weight: 700;vertical-align: middle;margin-left: 7px;}
}

.my-profile-edit{
  margin-top: 30px;margin-bottom: 35px;
  .img{
    position: relative;width: 120px;height: 120px;margin: 0 auto;display: flex;align-items: center;justify-content: center;
    &::before{content: '';border: 1px solid $border-color;border-radius: 50%;position: absolute;left: 0;top: 0;right: 0;bottom: 0;}
    img{width: 100%;height: 100%;object-fit: cover;border-radius: 50%;}
    img.default{object-fit: contain;width: auto;height: auto;}
  }
  .btn-profile-edit{
    position: absolute;right: 0;bottom: 10px;
    background: url('../images/icon-camera.svg') no-repeat 0 0;width: 30px;height: 30px;
  }
}

.address-delete{
  padding-top: 10px;margin-top: 25px;border-top: 2px solid #000;
  a{font-size: 14px;font-weight: 700;text-decoration: underline;}
}

.alarm-setting{
  .item{padding: 20px 0;border-bottom: 1px solid $border-color;}
  .title{font-size: 16px;}
  .desc{font-size: 12px;color: $gray-light;margin-top: 3px;}
}

.reward-process{
  padding: 15px 0;border-bottom: 1px solid $border-color;
  li{text-align: center;}
  .number{display: flex;align-items: center;justify-content: center;font-size: 20px;font-weight: 700; font-family: $font-family-eng;width: 40px;height: 40px;border-radius: 50%;background: $primary;margin: 0 auto;}
  .title{font-size: 12px;font-weight: 700;margin-top: 5px;}
  .icon-full-menu-arr{margin-top: 15px;}
}

.tip-divider{
  background: rgba(0,0,0,0.8);color: #fff;font-size: 10px;font-weight: 700;padding: 13px 20px;margin: 30px -20px;
}

.my-item-count{display: block;color: $sky;font-size: 18px;line-height: 1.111;}

.my-lucky-number-is{
  display: flex;align-items: center;font-size: 14px;color: rgba(0,0,0,0.5);line-height: 1;text-decoration: underline;
  a{font-size: 14px;color: rgba(0,0,0,0.5);line-height: 1;text-decoration: underline;}
  i{margin-right: 5px;transform: translateY(-1px);}
}

.system-guide{font-size: 14px;margin-bottom: 10px;color: rgba(0,0,0,0.5);margin-top: 18px;}

.my-vote-list{
  ul{display: flex;flex-wrap: wrap;margin: -5px;}
  li{padding: 5px;flex: 0 0 50%;}
}

.my-items{
  display: flex;flex-wrap: wrap;margin: -6px;
  .my-item-card{padding: 6px;flex: 0 0 50%;}
}

.my-item-card{
  .inner{
    padding: 12px;border: 1px solid $border-color;border-radius: 7px;display: flex;flex-direction: column;min-height: 190px;
  }
  .my-item-card-head{
    border-bottom: 1px solid $border-color;display: flex;justify-content: space-between;align-items: center;padding-bottom: 13px;flex: 0 0 auto;
  }
  .my-item-card-body{
    flex-grow: 1;display: flex;align-items: center;justify-content: center;
  }
  .my-item-card-foot{
    flex: 0 0 auto;
  }
  .btn-outline-light{border-radius: 2px;font-size: 18px;font-weight: 700;}
  .title{font-size: 16px;color: #000;line-height: 1.1176;}
  .number{font-size: 18px;font-weight: 700;font-family: 'Gilroy';line-height: 1.1578;}
  .status{
    line-height: 1;padding-top: 37px;
    &::before{content: '투표입력하기';font-size: 14px;font-weight: 700;color: $muted;line-height: 1.1428;}
  }
  .form-control{padding-left: 3px !important;padding-right: 3px !important;}

  &[data-type="reward"]{
    &[data-winning="Y"]{
      .my-item-card-head{border-color: rgba(255,255,255,.15);}
      .inner{background: $dark;border-color: $dark;}
      .title,
      .number{color: $primary;}
      .message{color: #fff;font-size: 16px;text-align: center;}
      .btn-outline-light{background-color: #fff;border-color: #fff;color: $sky;}
    }
    &[data-winning="N"]{
      .inner{background: #e6e6e6;border-color: #e6e6e6;}
      .btn-outline-light{background-color: #fff;border-color: #fff;}
    }
  }
  &[data-type="vote"]{
    &[data-voted="Y"]{
      .inner{background: $yellow;border-color: $yellow;}
      .status{
        display: flex;align-items: flex-end;width: 100%;justify-content: space-between;
        &::before{content: '입력완료'; color: #000;}
        &::after{content: '';background: url('../images/icon-vote-check.svg');width: 20px;height: 20px;}
      }
      .form-control{font-size: 24px;font-weight: 700;font-family: $font-family-eng;}
    }
  }
}

.modal-vote-card{
  .modal-dialog{max-width: 356px;}
  .modal-content{margin-left: 10px;margin-right: 10px;}
  .modal-body{padding-bottom: 20px;}
  .img{
    height: 0;padding-bottom: 100%;position: relative;
    img{width: 100%;height: 100%;object-fit: cover;position: absolute;border-radius: 50%;}
  }
  .text-lg{
    font-size: 28px;color: #fff;line-height: 1.35;margin-top: 20px;
  }
  .event-info{
    border-top: 1px solid rgba(255,255,255,.5);padding-top: 15px;margin-top: 15px;margin-bottom: 15px;
    .won-reward{font-size: 18px;color: $sky;font-weight: 700;}
    .product{font-size: 16px;color: #fff;margin-top: 5px;}
    .price{font-size: 16px;color: rgba(255,255,255,.6);}
    .desc{font-size: 16px;color: $gray;margin-top: 5px;}
  }
  &[data-winning="Y"]{
    .modal-content{background: #000;}
    .modal-title{color: $primary;}
    .btn-close{background-image: url('../images/icon-x-white.svg');}
  }
  &[data-winning="N"]{
    .modal-content{background: #e6e6e6;}
    .text-lg{color: #000;}
    .event-info{border-color: rgba(0,0,0,.5);}
  }
}

.result-open-animation{
  position: absolute;left: 0;top: 0;width: 100%;height: 100%;border-radius: 10px;overflow: hidden;
  > div{
    width: 100%;height: 100%;position: absolute;left: 0;top: 0;display: flex;align-items: center;justify-content: center;font-size: 200px;font-family: $font-family-eng;font-weight: 100;
    &:nth-of-type(1){background: $dark;color: $primary;}
    &:nth-of-type(2){background: $primary;}
    &:nth-of-type(3){background: $white;}
  }
}

.modal-vote-input{
  .title{
    border-bottom: 2px solid #000;display: flex;justify-content: space-between;align-items: center;padding-bottom: 15px;padding-top: 25px;
    span{font-size: 20px;color: #000;line-height: 1.15;}
    em{font-size: 20px;font-weight: 700;font-family: $font-family-eng;line-height: 1;margin-left: 5px;}
  }
  .feedback{font-size: 14px;color: $red;text-align: center;padding: 20px 0;}
  .form-control{
    font-family: $font-family-eng;font-weight: 700;
    &::placeholder{font-weight: 400;font-family: 'NanumBarunGothic';}
  }
  .button{
    margin: 30px -20px 0;
    .btn-round{border-radius: 0 0 7px 7px;}
  }
}

.modal-voted-ok{
  text-align: center;padding-top: 30px;
  &::before{content: '';display: inline-block;vertical-align: top;background: url('../images/icon-voted-check.svg');width: 55px;height: 51px;margin-bottom: 32px;}
  strong{font-size: 18px;margin-bottom: 20px;display: block;}
  p{font-size: 14px;line-height: 1.428;color: rgba(0,0,0,0.6);margin-bottom: 46px;}
}

.modal-vote-is{
  padding-top: 50px;
  .text{font-size: 18px;line-height: 1.2222;margin-bottom: 15px;}
  .text-lg{font-size: 22px;line-height: 1.3636;font-weight: 700;}
  .vote-step{
    > li{
      display: flex;padding: 20px 0;
      &::before{content: '';display: block;background-position: 0 0;background-repeat: no-repeat;background-size: contain;flex: 0 0 60px;margin-right: 15px;}
      @for $i from 1 through 3 {
        &:nth-child(#{$i}){
          &::before{background-image: url('../images/icon-vote-#{$i}.svg')};
        }
      }
      div{font-size: 13px;line-height: 1.538;}
    }
    > li + li{border-top: 1px solid $border-color;}
  }
}

.vote-wrap{
  .content{
    &.vote{
      width: 100%;height:100%;position: relative;z-index: 2;padding: 0 0 35px 0;
      .desc{
          p{margin-top:30px;}
      }
      .form-control{margin-top:15px;
          &:focus{border:1px solid $black;}
      }
      .bg-balls {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;height: 100%;z-index:-1;
      }
      .input-form{position: relative;}
      .error-msg{color:$red;font-size:10px;position: absolute;right:10px;top:49%;
          transform: translate(0, -50%);opacity: 0;
          &.active{opacity: 1;}
      }
    }
  }
}

.balls{
  &.back{
      position: fixed;left: 0;top: 0;width: 100vw;height: 100vh;z-index: -1;overflow: hidden;
      .bg-balls {
          position: absolute;left: 0;top: 0;width: 100%;height: 100%;z-index:-1;
      }
      & + .wrap{z-index: 1;}
  }
  &.front{
      position: fixed;left: 0;top: 0;width: 100vw;height: 100%;z-index: 999;overflow: hidden;background: #fff;
      .bg-balls {
          position: absolute;left: 0;top: 0;width: 100%;height: 100%;
      }
  }
}

.vote-loading{
  width: 240px;height: 240px;background: #000;border-radius: 50%;position: absolute;left: 50%;top: 50%;transform: translate(-50%, calc(-50% - 75px));
  .set{width: 100%;height: 100%;display: flex;flex-direction: column;align-items: center;justify-content: center;text-align: center;position: absolute;left: 0;top: 0;transition: opacity .2s;}
  p{color: #fff;font-size: 18px;font-weight: 700;line-height: 1.3333;margin-top: 20px;}
  .after{
      opacity: 0;
      img{width: 30px;}
      p{margin-top: 34px;}
  }
  &.active{
      .before{opacity: 0;}
      .after{opacity: 1;}
  }
}

.review-form{
  .review-form-title{
    display: flex;align-items: center;margin-bottom: 15px;margin-top: 40px;
    p{font-weight: 16px;font-weight: 700;}
    small{font-size: 12px;color: $gray-light;font-weight: 400;margin-left: 2px;}
    .badge{margin-left: 5px;}
  }
  .review-form-title-desc{font-size: 12px;color: $gray;word-break: keep-all;}
}

.checkbox-button-wrap{
  .form-check + .form-check{margin-top: 10px;}
}

.form-photo-upload{
  margin-top: 10px;
  .btn-upload{
    width: 100px;height: 100px;display: flex;flex-direction: column;align-items: center;justify-content: center;border: 1px solid $border-color;border-radius: 7px;font-size: 14px;color: #ccc;margin-bottom: 15px;
    i{display: inline-block;width: 20px;height: 20px;background: url('../images/icon-plus.svg') no-repeat 0 0;margin-bottom: 5px;}    
  }
}

.uploded-photos{
  margin-left: -20px;margin-right: -20px;
  .item{display: block;}
  .img{
    height: 0;padding-bottom: 100%;position: relative;
    img{width: 100%;height: 100%;object-fit: cover;position: absolute;border-radius: 7px;}
  }
  button{
    width: 20px;height: 20px;background-color: rgba(0,0,0,0.5);border-radius: 50%;overflow: hidden;display: flex;align-items: center;justify-content: center;position: absolute;top: 10px;right: 10px;
    //i.icon-x-white{width: 16px;height: 16px;}
  }
  .swiper-slide{width: 100px !important;}
  .swiper:not(.swiper-initialized){
    padding-left: 20px;
    .swiper-slide{margin-right: 5px; }
  }
}